.slide_up_text * {
  overflow: hidden;
  display: block;
  transform: translate(0, 150%);
  transition-delay: 0s;
  transition: $slide_up_text;
}
.slide_up_text.slide_up_text--active * {
  display: block;
  transition: $slide_up_text;
  transform: translate(0, 0%);
}
.slide_up_text.slide_up_text--active li:nth-child(1) > a,
.slide_up_text.slide_up_text--active div:nth-child(1) > a,
.slide_up_text.slide_up_text--active li:nth-child(1) > span,
.slide_up_text.slide_up_text--active div:nth-child(1) > span {
  transition-delay: 0s;
}
